@import '../../var.css';

.navbar{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 2;
    margin: 0 10%;
    padding: 5px;
}

.title{
    font-size: 30px;
    font-weight: 600;
    text-decoration: none;
    color: var(--color-text);
}
.menuBtn{
    display:none;
}
.menu{
    display: flex;
}
.menuItem{
    display: flex;
    gap: 47px;
    list-style: none;
}
.menuItem a{
    font-size: 15px;
    color: var(--color-text);
    text-decoration: none;
}

@media screen and (max-width: 830px){
    .navbar{
        flex-direction: column;
        align-items: flex-start;
    }
    .menu{
        position: absolute;
        /* margin-right: 10%; */
        right: 0;
        align-items: end;
        flex-direction: column;
        gap: 11px;
        z-index: 3;
    }
    .menuBtn{
        display: block;
        cursor: pointer;
        height: 30px;
        margin: 10px;
    }
    .menuItem{
        display: none;
        flex-direction: column;
        align-items: center;
        gap: 13px;
        border-radius: 10px;
        background-color: rgb(25,55,109);
        background: linear-gradient(
      0deg,
      rgba(25, 55, 109, 0.2) 0%,
      rgba(25, 55, 109, 1) 100%
    );
    padding: 24px 33px;
    box-shadow: 0 0 4px 0 rgba(0,0,0,0.25);
    }
    .menuOpen{
        display: flex;
        z-index: 1;
    }
}